@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
}

.slider {
  :global {
    .swiper-wrapper {
      @include resetList;
    }
  }
}

.button {
  @include resetButton;

  position: absolute;
  top: 8rem;
  right: calc(var(--padding-container-h) + 6rem);
  padding: 1rem;
  background-color: $colorShader;
  transition-duration: $transitionDuration;
}

.buttonNext {
  right: var(--padding-container-h);
  left: auto;
  transform: rotate(180deg);
}

.buttonDisabled {
  opacity: 0.5;
  pointer-events: none;
}

.icon {
  width: 3rem;
}
