@import "styles/variables";
@import "styles/mixins";

.heading {
  margin: 0;
}

.h1 {
  @include h0($colorLightest);
}

.h2 {
  @include h1($colorLightest);
}

.h3 {
  @include h2($colorDarkest);
}

.h4 {
  @include h4($colorDarkest);
}

.headingDecorated {
  position: relative;
  display: inline-block;
  color: $colorDarkest;

  &::before {
    content: "";
    position: absolute;
    bottom: -0.5rem;
    left: -1rem;
    width: calc(100% + 2rem);
    height: 2rem;
    background-color: $colorHighlightDark;
    z-index: -1;
    opacity: 0.5;
  }
}
