@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: 60% 1fr;
  grid-gap: 5rem;
}

.image {
  width: 100%;
  height: 50rem;
  object-fit: cover;
}

.descriptor {
  @include regular($colorDarkest);

  display: flex;
  flex-direction: column;
  margin: 2.5rem 0 0 0;
}
