@import "styles/variables";
@import "styles/mixins";

.address {
  @include h5($colorDarkest);

  margin: 0.5rem 0 0 0;
  opacity: 0.5;
}

.preview {
  @include resetButton;

  width: 100%;
  height: 35rem;
  background-size: cover;
}

.full {
  display: block;
  margin: 15vh auto 0 auto;
  width: 100rem;
}

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5rem;
  margin-top: 2rem;
}

.subheading {
  margin-top: 5rem;
  text-align: center;
}

.features {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  margin-top: 3rem;
}

.feature {
  @include regular($colorDarkest);

  position: relative;
  padding-top: 7rem;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: calc(50% - 2rem);
    width: 4rem;
    height: 4rem;
    background-size: cover;
  }
}

.featureDesign {
  &::before {
    background-image: url("../../assets/icons/i_design.svg");
  }
}

.featureLocation {
  &::before {
    background-image: url("../../assets/icons/i_location.svg");
  }
}

.featurePeople {
  &::before {
    background-image: url("../../assets/icons/i_neighbours.svg");
  }
}

.featureInfrastructure {
  &::before {
    background-image: url("../../assets/icons/i_infrastructure.svg");
  }
}
